    'home.mid.pediatric.content':'Детский массаж, как способ лечения, основывается на концепции единого целого и основных диагностических и лечебных принципах традиционной китайской медицины.  Применение детского массажа, а именно специальных методов работы на конкретных  поверхностях тела ребенка, изменяет и регулирует физиологическое состояние организма, останавливает патологические процессы, повышает иммунитет детского организма, тем самым достигая основной цели - профилактики и лечения болезней. Детский массаж относится к наружным методам лечения традиционной китайской медицины и является важным разделом мануальной терапии.',
